.profile {
    display              : grid;
    grid-template-rows   : auto auto 1fr;
    grid-template-columns: auto auto 1fr;
    grid-gap             : 10px 5px;
    width                : 100%;
    height               : 100%;
}

.profile--image {
    grid-row     : 1/3;
    grid-column  : 1;
    border-radius: 50%;
}

.profile--name {
    grid-row    : 1;
    grid-column : 2/4;
    align-self  : end;
    padding-left: 10px;
}


.profile--jobtitle {
    padding-left: 10px;
    grid-row    : 2;
    grid-column : 2/3;
    align-self  : start
}

.profile--company {
    grid-row   : 2;
    grid-column: 3;
    align-self : start
}

.profile--review {
    grid-row    : 3;
    grid-column : 1/-1;
    padding-left: 10px;
    text-indent : 40px;
}